<template>
    <modal title="" transition="fadeDown" :is-show="visible" @close="close" ok-text="" cancel-text="">
        <form @submit.prevent="validateForm" class="pad-h is-10 column is-offset-1">

           <div class="field">
                <div class="control is-horizontal">
                   <div class="control-label">
                       <label class="label"></label>
                   </div>
                   <div class="control">
                       <input class="input" type="password" placeholder="" name="orgPasssword" v-model="passwordForm.orgPasssword" v-validate="'required'" :class="{'is-danger': errors.has('orgPasssword')}">
                   </div>
               </div>
               <span class="help is-danger is-horizontal" v-show="errors.has('orgPasssword')">{{ errors.first('orgPasssword') }}</span>
           </div>

            <div class="field">
                <div class="control is-horizontal">
                    <div class="control-label">
                        <label class="label"></label>
                    </div>
                    <div class="control">
                        <input class="input" type="password" placeholder="" name="newPassword" v-model="passwordForm.newPassword" v-validate="'required'" :class="{'is-danger': errors.has('newPassword')}">
                    </div>
                </div>
                <span class="help is-danger is-horizontal" v-show="errors.has('newPassword')">{{ errors.first('newPassword') }}</span>
            </div>

           <div class="field">
                <div class="control is-horizontal">
                   <div class="control-label">
                       <label class="label"></label>
                   </div>
                   <div class="control">
                       <input class="input" type="password" placeholder="" name="rePassword" v-model="passwordForm.rePassword" v-validate="'required'" :class="{'is-danger': errors.has('rePassword')}">
                   </div>
               </div>
               <span class="help is-danger is-horizontal" v-show="errors.has('rePassword')">{{ errors.first('rePassword') }}</span>
           </div>

        </form>
        <div slot="footer"><a class="button" @click="close"></a> <a class="button is-primary" @click="validateForm"></a></div>
    </modal>
</template>
<script>
import api from 'src/api/index.js'

export default {
    props: ['visible'],
    data() {
        return {
            passwordForm: {
                orgPasssword: '',
                newPassword: '',
                rePassword: ''
            }
        }
    },
    computed: {
        panelId() {
            return this.$store.getters.user.panelId
        }
    },
    methods: {
        close() {
            this.$emit('close')
        },
        validateForm(scope) {
            this.$validator.validateAll().then(() => {
                const {orgPasssword, newPassword} = this.passwordForm
                api.changePwd({
                    panelId: this.panelId,
                    orgPasssword,
                    newPassword
                }).then(res => {
                    const { msg, result } = res.data;
                    if (result !== 'ok') {
                        this.$notify.warning({ content: msg || '' })
                        return false;
                    }

                    this.$notify.success({ content: msg || '' })
                }).catch((error) => {
                    console.log(error)
                })
            })
        }
    }
}
</script>
<style lang="scss" scoped>

</style>


